.box {
    border-radius: 5px;
    -webkit-box-shadow: 0 2px 3px rgba(17,17,17,.1), 0 0 0 1px rgba(17,17,17,.1);
            box-shadow: 0 2px 3px rgba(17,17,17,.1), 0 0 0 1px rgba(17,17,17,.1);
    display: block;
    padding: 20px;
    background-color: #ffffff;
    -webkit-transform: scale(1.00);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.box:hover {
    -webkit-transform: scale(1.02);
    -webkit-transition: 0.25s;
    -o-transition: 0.25s;
    transition: 0.25s;
    background-color: lightgrey;
}

.related-card {
    display: block;
    font-size: 13px;
    font-weight: 500;
    color : darkslategrey;
}


